.home {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: linear-gradient(to right,
            #FEB028 0%,
            #FEB028 50%,
            white 50%,
            white 100%);

}

.mainContentDiv {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
    margin-bottom: 5%;

}

.mainContent {
    display: inline;
    align-self: center;
    text-align: center;
    font-size: 100px;
    font-weight: 800;
    align-self: center;


}

.tagLine{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

button {
    height: 40px;
    width: 120px;
    border-radius: 50px;
    text-align: center;
}

.myButton {
    border: gray;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
        rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    margin-top: 10px;
}

.myButton:hover {
    background-color: black;
    color: white;
    transition: 0.2s 0.1s;
}

@media (max-width: 1000px) {

    .home {
        background: #FEB028;
    }

    .mainContent {
        font-size: 85px;
        font-weight: 700;

    }
    
    img {
        width: 280px;
    }


}

@media (max-width: 750px) {

    .mainContentDiv {
        width: 40%;
        justify-content: center;
        

    }

    .tagLine{
        align-items: center;

    }
    
    img {
        width: 280px;
    }


}